.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
    background-image: url(florida-beach-sunset-1920x1080.jpg);
    background-size: cover;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: bold;
    height: 100vh;
}

.container {
    width: 80%;
    height: 40px;
    position: relative;
    top: 40px;
    margin: 0 auto;
}

#logo {
    margin-top: -180px;
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    font-size: 120%;
    font-family: 'Monda';
}

.navigation-wrapper {
    position: relative;
}

.navigation-button {
    will-change: transform;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    position: fixed;
    z-index: 1;
    top: 40px;
    right: 100px;
    background: transparent;
    cursor: pointer;
}

.navigation-button .fa {
    border: 2px solid white;
    border-radius: 3px;
    padding: 10px;
    color: white;
}

.navigation-menu ul li {
    list-style: none;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-weight: 300;
    color: hsl(0, 0%, 70%);
}

.navigation-menu {
    content: '';
    position: fixed;
    top: 0;
    right: 0;
    width: 50%;
    background: white;
    height: 100%;
    transform: skewX(0deg) translate(100%, 0);
    transform-origin: top right;
    transition: all .2s ease-in;
    z-index: -1;
}

.navigation-menu ul {
    transform: skewX(-8deg);
    transform-origin: top left;
    position: fixed;
    right: 120px;
    top: 120px;
    width: 400px;
    text-align: right;
}

.navigation-menu ul li {
    position: relative;
    z-index: 999;
    font-size: 32px;
    color: hsl(0, 0%, 10%);
    line-height: 64px;
}

.navigation-menu ul li a {
    border: none;
    color: hsl(0, 0%, 10%);
    text-decoration: none;
}

.navigation-menu.active {
    transform: skew(8deg) translate(0, 0);
}

.navigation-menu li {
    opacity: 0;
    transform: translate(0, 10px);
    transition: all 0s ease-in .3s;
}

.navigation-menu.active li {
    opacity: 1;
    transform: translate(0, 0);
    transition: all 0.2s ease-in 0s;
}


.navigation-menu.active li:nth-child(1) {
    transition-delay: .3s;
}

.navigation-menu.active li:nth-child(2) {
    transition-delay: .4s;
}

.navigation-menu.active li:nth-child(3) {
    transition-delay: .5s;
}

.navigation-menu.active li:nth-child(4) {
    transition-delay: .6s;
}

.navigation-menu.active li:nth-child(5) {
    transition-delay: .7s;
}

.navigation-menu.active li:nth-child(6) {
    transition-delay: .8s;
}

.hero h1 {
    margin-top: 178px;
    text-transform: uppercase;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 26px;
    letter-spacing: 4px;
    color: white;
    z-index: -50;
}

span {
    font-size: 65px;
    letter-spacing: 2px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.button {
    font-family: Tahoma;
    font-weight: normal;
    font-size: 12px;
    margin-top: 40px;
}

.btn1:hover {
    text-decoration: none;
    color: white;
}

.btn2:hover {
    text-decoration: none;
    color: black;
}

.btn1 {
    padding: 12px 25px;
    background: #f20c4a;
    text-decoration: none;
    color: white;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;

}

.btn2 {
    padding: 12px 25px;
    background: #f4f6f8;
    text-decoration: none;
    color: #000;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
